import request from '../utils/request'
import { msg, errorMsg } from '../utils/common'
import { mapState } from 'vuex'
export default {
  name: 'detail',
  data () {
    return {
      show: false
    }
  },
  computed: {
    ...mapState({
      isRegAuth: state => state.user.isRegAuth,
      isReg: state => state.user.isReg,
      openid: state => state.user.openid,
      userType: state => state.user.userType
    }),
    needBook () {
      if (this.isRegAuth && this.isReg) {
        if (this.userType === 'student') {
          return true
        } else {
          return false
        }
      } else {
        return true
      }
    }
  },
  props: {
    item: Object
  },
  methods: {
    showBox () {
      this.show = true
    },
    hideBox () {
      this.show = false
    },
    book (item) {
      if (!this.isRegAuth) {
        // 没授权过
        this.$router.push({ name: 'selRegType' })
      } else if (!this.isReg) {
        // 没注册过
        this.$router.push({ name: 'register' })
      } else {
        // 信息都全，往下
        this._book(item)
      }
    },
    _book (item) {
      request({
        url: 'http://www.zhiheyikaoqin.cn/stu/order?stuOpenid=' + this.openid + '&courserId=' + item.teaCourse.courseId,
        method: 'get'
      }).then(res => {
        if (res.data.code !== 0) {
          errorMsg(this, res.data.msg)
        } else {
          msg(this, '请等待预约结果', '220px')
        }
        this.hideBox()
      })
    }
  }
}



