import request from '../utils/request'
import courseDetail from './detail'
import noData from '../components/noData'
import loading from '../components/loading'
export default {
  name: 'course',
  components: { courseDetail, noData, loading },
  data () {
    return {
      loading: false,
      all: [],
      detailItem: {}
    }
  },
  mounted () {
    this.loading = true
    this.getAll()
  },
  methods: {
    deepClone (original) {
      return JSON.parse(JSON.stringify(original))
    },
    getAll () {
      request({
        url: 'http://www.zhiheyikaoqin.cn/stu/findAllCourse?page=1&size=100',
        method: 'GET'
      }).then(res => {
        this.loading = false
        if (Number(res.data.code) !== 0) {
          this.all = []
        } else {
          const data = res.data.data
          console.log(data)
          this.all = data.map(item => {
            item.teaCourse.time = this.getTime(item.teaCourse.courseStartTime) + ' - ' + this.getTime(item.teaCourse.courseEndTime)
            item.status = this.getStatus(Number(item.teaCourse.courseStatus))
            return item
          })
        }
      // }, 0)
      })
    },
    openDetail (item) {
      this.detailItem = this.deepClone(item)
      this.$nextTick(() => {
        this.$refs.detail.showBox()
      })
    },
    getTime (str) {
      const hour = str.split(' ')[1].split(':')[0]
      const min = str.split(' ')[1].split(':')[1]
      return hour + ':' + min
    },
    getStatus (flag) {
      switch (flag) {
        // case 300:
        //   return '待预约'
        // case 301:
        //   return '已预约'
        case 302:
          return '上课中'
        case 303:
          return '已结束'
        case 304:
          return '已失效'
        default:
          return '可预约'
      }
    }
  }
}



